<template>
     <ul class="catalog__pagination pagination">
          <li class="pagination__item">
            <a class="pagination__link pagination__link--arrow pagination__link--disabled"
            v-on:click="paginate(page-1)"
            aria-label="Предыдущая страница">
              <svg width="8" height="14" fill="currentColor">
                <use xlink:href="#icon-arrow-left"></use>
              </svg>
            </a>
          </li>
          <li class="pagination__item" v-for="pageNumber in pages" v-bind:key="pageNumber">
            <a href="#"  class="pagination__link" v-bind:class="{'pagination__link--current': pageNumber === page}"
            v-on:click.prevent="paginate(pageNumber)">
              {{ pageNumber }}
            </a>
          </li>
          <li class="pagination__item">
            <a class="pagination__link pagination__link--arrow" href="#"
            v-on:click="paginate(page+1)" aria-label="Следующая страница">
              <svg width="8" height="14" fill="currentColor">
                <use xlink:href="#icon-arrow-right"></use>
              </svg>
            </a>
          </li>
        </ul>
</template>

<script>
export default ({
    model: {
prop: 'page',
event: 'paginate',
    },
    props: ['page', 'count', 'perPage'],
    computed: {
        pages() {
            return Math.ceil(this.count / this.perPage);
        },
    },
    methods: {
        paginate(page) {
          if (page < 1 || page > this.pages) {
          return;
          }
this.$emit('paginate', page);
window.scrollTo(0, 0);
        },
    },
});
</script>
